package game

import Constants
import Stats
import kotlinx.browser.document
import kotlinx.browser.window
import popup

class Game(var letters: Int, val attempts: Int, val stats: Stats) {
    var recordedWord = ""
    var activeRow = 0
    val wordList = mutableListOf<String>()
    var activeWordList = listOf<String>()
    var gameWord = ""
    var gameSolved = false
    var gameLost = false
    val playable: Boolean
    get() {
        return !gameSolved && !gameLost
    }
    var guessDatabase = mutableListOf<Guess>()
    val gridRate: Double
    get() {
        return letters.toDouble()/attempts.toDouble()
    }
    var lastGameData = GameData("",0,"")
    val statValues: MutableList<String>
    get() {
        return stats.statValuesList
    }



    fun loadWords(filepath: String, andStart: Boolean = false, seed: Int = -1) {
        val url = window.location.href.split("?")[0]
        Constants.dictionary.getAsync(url + filepath) { list ->
            wordList.addAll(list.lines())
            if (andStart) {
                startRound(seed = seed)
            }
        }
    }

    fun startRound(letters: Int = this.letters, seed: Int = -1) {
        activeRow = 0
        guessDatabase.clear()
        gameSolved = false
        gameLost = false
        if (letters!=this.letters){
            this.letters = letters
        }
        activeWordList = wordList
        if (seed == -1) {
            selectWord(activeWordList.indices.random())
        } else {
            selectWord(seed)
        }
    }

    fun selectWord(seed: Int = -1) {
        gameWord = activeWordList[seed]
    }

    fun createChallenge(): String {
        return window.location.href.split("?")[0] + "?seed=" + activeWordList.indexOf(gameWord)
    }

    fun addLetter(c: Char) {
        if (recordedWord.length < letters) {
            recordedWord += c
        } else {
            // too long warning raises here
        }
    }

    fun removeLetter() {
        recordedWord = recordedWord.dropLast(1)
    }

    fun checkWord(): CheckReturn {
        if(activeWordList.isEmpty()){
            window.alert("kelime yüklenmedi")
            return CheckReturn.ERROR
        }

        if (recordedWord.length != letters) { // not enough letters
            return CheckReturn.LESS_LETTERS
        }

        if(recordedWord=="pungo"){
            return CheckReturn.PUNGO
        }

        if(recordedWord !in activeWordList){ // not a word
            return CheckReturn.UNLISTED_WORD
        }

        // rock & roll
        val newGuesses = (0 until letters).map {i-> Guess(recordedWord[i],i,-1,activeRow)}
        val gamePairs = (0 until letters).map { i-> Pair(i,gameWord[i]) }.toMutableList()


       // var miniRecord = gameWord.toMutableList()
        var miniIndex = 0
        newGuesses.forEachIndexed {index, it->
            if(gameWord[it.slot]==it.letter){
                newGuesses[index].result = GuessResult.RIGHT_PLACE
                newGuesses[index].matchSlot = gamePairs[miniIndex].first
                gamePairs.removeAt(miniIndex)
            }else{
                miniIndex += 1
            }
        }

        if(gamePairs.isEmpty()){
            gameSolved = true
            guessDatabase.addAll(newGuesses)
            return CheckReturn.SOLVED
        }

        newGuesses.forEachIndexed{index, it->
            if(it.result!=GuessResult.RIGHT_PLACE){
                val ind = gamePairs.indexOfFirst {gp-> gp.second==it.letter  }
                if(ind!=-1){
                    newGuesses[index].result = GuessResult.WRONG_PLACE
                    newGuesses[index].matchSlot = gamePairs[ind].first
                    gamePairs.removeAt(ind)
                }else{
                    newGuesses[index].result = GuessResult.WRONG
                }
            }
        }

        guessDatabase.addAll(newGuesses)
        if(activeRow<attempts){
            recordedWord = ""
            activeRow+=1
        }else{

            gameLost = true
        }
        return CheckReturn.UNSOLVED
    }

    fun updateLastGameData(activeRow: Int, time: String){
        lastGameData = GameData(gameWord, activeRow, time)
    }

    fun challenge() {
        val link = createChallenge()
        val text: String
        if (gameSolved) {
            println("SUCCESS")
            text = "Ben ${activeRow + 1} tahminde sözcüğü buldum, bakalım sen kaç tahminde bulacaksın?\n"
        } else if (activeRow == 6) {
            println("FAILURE")
            text = "Ben başaramadım, bakalım sen sözcüğü bulabilecek misin?\n"
        } else {
            println("YET TO TRY")
            text = "Bu sözcük için sana meydan okuyorum!\n"
        }


        try{
            if (js("if(navigator.share){return true}") as Boolean) {
                js("navigator.share({text: text, url: link}).catch(console.error);")
            } else {
                window.navigator.clipboard.writeText((text + link))
                document.getElementById("main")!!.append(popup("Link kopyalandı! Paylaşmaya Hazır!"))
            }
        }catch (e: Exception){
            window.navigator.clipboard.writeText((text + link))
            document.getElementById("main")!!.append(popup("Link kopyalandı! Paylaşmaya Hazır!"))
        }

    }


    data class GameData(val word: String, val attempts: Int, val time: String){
        val isValid: Boolean get() {return word!=""}

        val wordUppercase: String
            get(){
                return word.toList().map { turkishUppercase(it) }.joinToString("")
            }

        private fun turkishUppercase(c: Char): Char {
            return if(c=='ı'){
                'I'
            }else if(c=='i'){
                'İ'
            }else{
                c.uppercaseChar()
            }
        }

    }



    enum class CheckReturn{
        ERROR {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = ""
        },
        LESS_LETTERS {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Yetersiz harf"
        },
        PUNGO {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Biz de sizi çok seviyoruz."
        },
        UNLISTED_WORD {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Böyle sözcük olmaz olsun."
        },
        SOLVED {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = "Cevap bulundu, yürü be."
        },
        UNSOLVED {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = ""
        };

        abstract val warningNeeded: Boolean
        abstract val warningText: String



    }
}