data class Stats(
    var played: Int = 0,  // oynanan toplam oyun
    var win: Int = 0,  // kazanılan toplam oyun
    var streak: Int = 0, // şu anki seri
    var maxStreak: Int = 0, // en uzun seri
    var timeRecord: Int = 0,
    var distribution: MutableList<Int> = MutableList(6) { 0 }
){
    val statValuesList: MutableList<String>
        get() {
            return mutableListOf(
                played.toString(),
                win.toString(),
                streak.toString(),
                maxStreak.toString(),
                timeRecord.toString()
            )
        }

}